* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#background {
  box-sizing: border-box;
  background: url("../imgs/bg-layout.jpg") center center no-repeat;
  background-size: cover;
  width: 100%;
  height: 1000px;
  background-color: #000;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: sticky;
  top: 0;
  border-bottom: #fff solid 1px;
  z-index: 1020;
  height: 100px;
}

.nav.active {
  background-color: #fff;
  transition: 0.5s ease-in-out;
}

.nav.active .search-btn{
  background-color: #000;
  color: #fff;
}

.logo-container img{
  width: 80px;
  border-radius: 50%;
}

#navbar {
  display: flex;
  align-items: center;
  justify-content: center;
}

.search-btn {
  box-sizing: unset;
  color: #000;

  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 0;
  background: #fff;
}
.search-btn.active{
  background-color: #000;
}
.search-btn.active i {
  color: #fff;
}
.search-btn:hover {
  cursor: pointer;
}

#navbar li {
  list-style: none;
  padding: 0 25px;
  position: relative;
  border-right: 1.6px solid #000;
  transition: 0.5s;
}

#navbar li a {
  font-family: Arial, sans-serif;
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: 600;
  color: #000;
  transition: 0.5s;
}
.change-color nav ul li {
  list-style: none;
  padding: 0 25px;
  position: relative;
  border-right: 1.6px solid #fff !important;
}

.change-color nav ul li a {
  color: #fff !important;
}
.change-color nav ul li a:hover {
color: #000 !important;
}
.change-color nav ul li:last-child {
  border-right: 0 !important;
}
.ul-bg.active #navbar a {
  color: #000 !important;
}


.ul-bg.active #navbar li {
  position: relative;
  border-right: 1.6px solid #000 !important;
  transition: 0.5s;
}
.ul-bg.active #navbar li:last-child {
  border-right: 0 !important;
}
#navbar li a:focus {
  color: #000;
}
#navbar li a:focus::after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #000;
  position: absolute;
  bottom: -38px;
  left: 0;
}
#navbar li:last-child {
  border-right: 0;
}

#navbar li a:hover {
  color: #000;
}

#navbar li a::after {
  content: "";
  width: 0;
  height: 2px;
  background-color: #000;
  position: absolute;
  bottom: -38px;
  left: 0;
  transition: width 0.3s ease; /* Add a transition for smooth animation */
}

#navbar li a:hover::after {
  width: 100%;
}

.toggle-menu i {
  color: #fff;
}
.toggle-menu {
  display: none;
}
@media screen and (max-width: 767px) {
  .search-box {
    position: fixed;
    right: 60px;
    border-radius: 40px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .toggle-menu {
    display: block;
    position: fixed;
    left: 30px;
    z-index: 1022;
  }
  .nav.active .toggle-menu i{
    color: #000;
    
  }
  .fas.fa-times{
    transition: 0.5s;
  }
  .nav.active .fas.fa-times,
  .nav.active #navbar li a{
    color: #fff !important;
  }
  .nav.active #navbar li{
    border: none !important;
  }
  .toggle-menu i {
    font-size: 15px;
    cursor: pointer;
  }
  .toggle-menu.active i{
    color:#000}
    .logo-container img{
      height: 65px;
      width: 60px;
    }
  
  #navbar li:first-child {
    margin-top: 50px;
  }

  #navbar li {
    
    margin-bottom: 25px;
    border-right: 0 !important;
  }
  
  .nav.active{
    display: flex;
    justify-content: space-around;
    z-index: 1021;
    position: sticky;
  }

  #navbar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
  z-index: 1020;
    top: 0;
    left: -600px;
    height: 100%;
    width: 300px;
    background-color: #212121;
    box-shadow: 0 40px 60px rgba(0, 0, 0, 0.4);
    padding: 40px 0 0 300px;
    transition: 0.5s ease-in-out;
  }

  #navbar.active {
    left: 0px;
    padding: 40px 0 0 10px;
  }
  .ul-bg.active #navbar li {
    border: none;
  }
  .ul-bg.active #navbar a {
    color: #fff;

  }
  #navbar li a:focus::after {
    content: "";
    width: 0;
    height: 0;
    background-color: transparent;
  }
  #navbar li a:hover {
    color: #fff;
  }
  #navbar li a:hover::after {
    content: "";
    width: 0%;
    height: 0;
    background-color: transparent;

    color: #fff;
    left: 0;
  }
}
