/* ProductCard.css */



.product-cont{
  margin: 15px 15px 40px;
}
.products-section {
display: grid;
grid-template-columns: repeat(4, 1fr);
grid-template-rows: repeat(2, 1fr);
  
gap: 15px;
justify-content: center;
overflow: hidden;

}

.product-card {
margin: auto;
overflow: hidden;
width: 100%;
height: auto;
max-width: 440px; /* Set a maximum width to control the size on larger screens */
cursor: pointer;
}

.product-card img {
width: 100%;
height: 400px;
}

.product-details {
margin-bottom: 10px;
}

.product-name {
  font-family: "Raleway", sans-serif;
font-weight: 400;
color: #212129;
font-size: 1.2em;
margin-bottom: 5px;
text-align: start;
}

.price {
  font-family: "Raleway", sans-serif;
font-weight: 400;
color: #595959;
margin-bottom: 5px;
}

.availability {

font-weight: bold;
}

.in-stock {
  font-family: "Raleway", sans-serif;
font-weight: 500;
color: green;
}

.out-of-stock {
color: #CF9362;
}
@media screen and (max-width: 1213px) {
  .products-section {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(4, 1fr);
      
    }
    .product-card {
      margin: auto;
      overflow: hidden;
      
      height: 500px;
max-width: 500px; /* Set a maximum width to control the size on larger screens */

    }

}
/* button */
.middle-background {
  position: absolute;
  top: 40%;
  left: 40%;
  z-index: 1;
}
.middle-background button {
  width: 150px;
  border: unset;
  padding: 15px 25px;
  color: #212121;
  z-index: 1;
  background: #fff;
  position: relative;
  font-size: 17px;
  font-family:'Raleway', sans-serif;
  /* -webkit-box-shadow: 4px 8px 19px -3px rgba(0, 0, 0, 0.27); */
  /* box-shadow: 4px 8px 19px -3px rgba(0, 0, 0, 0.27); */
  transition: all 500ms;
  overflow: hidden;
  outline: 1px solid #fff;
  cursor: pointer;
}


.middle-background button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0;
  background-color: #000;
  z-index: -1;
  /* box-shadow: 4px 8px 19px -3px rgba(0, 0, 0, 0.27); */
  transition: all 250ms;
  overflow: hidden;

}

.middle-background button:hover {
  color: #e8e8e8;
}

.middle-background button:hover::before {
  width: 100%;
}

/* footer */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');


.container-footer{
	max-width: 1170px;
	margin:auto;

    
}
.row-footer{
	display: flex;
	flex-wrap: wrap;
}
.footer-ul{
	list-style: none;
}
.footer{
	background-color: #151208d5;
    padding: 70px 0;
    position:relative !important;
    bottom: 0;
	width: 100%;
	line-height: 1.5;
	font-family: 'Poppins', sans-serif;
}
.footer-col{
   width: 25%;
   padding: 0 15px;
}
.footer-col h4{
	font-size: 18px;
	color: #ffffff;
	text-transform: capitalize;
	margin-bottom: 35px;
	font-weight: 500;
	position: relative;
	line-height: 1.5;
	font-family: 'Poppins', sans-serif;
}
.footer-col h4::before{
	content: '';
	position: absolute;
	left:2px;
	bottom: -10px;
	background-color: #fff;
	height: 2px;
	box-sizing: border-box;
	width: 50px;
}
.footer-col ul li:not(:last-child){
	margin-bottom: 10px;
}
.footer-col ul li a{
	font-size: 16px;
	text-transform: capitalize;
	color: #ffffff;
	text-decoration: none;
	font-weight: 300;
	color: #bbbbbb;
	display: block;
	transition: all 0.3s ease;
}
.footer-col ul li a:hover{
	color: #ffffff;
	padding-left: 8px;
}
.footer-col .social-links a{
	display: inline-block;
	height: 40px;
	width: 40px;
	background-color: rgba(255,255,255,0.2);
	margin:0 10px 10px 0;
	text-align: center;
	line-height: 40px;
	border-radius: 50%;
	color: #ffffff;
	transition: all 0.5s ease;
}
.footer-col .social-links a:hover{
	color: #24262b;
	background-color: #ffffff;
}

/*responsive*/
@media(max-width: 768px){
  .footer-col{
    width: 50%;
    margin-bottom: 30px;
	margin-left: 30px;
}

}
@media(max-width: 574px){
  .footer-col{
    width: 100%;
}
}